import React, {memo, useCallback, useEffect, useRef, useState} from 'react';
import {
  AnimatedKeyboardAvoidingViewRef,
  FullScreenWrapper,
  MyKeyboardAvoidingView,
  UIInput,
  UIText,
  UITextInput,
  UIUserPickerInput,
} from '@true/ui';
import {AnimatedBottomSpace} from '@/components/AnimatedBottomSpace';
import {
  goBack,
  styled,
  ToastService,
  useAsyncFn,
  useAutoToastError,
  useBoolean,
  useNavigationParams,
} from '@true/core';
import {
  runOnUI,
  useAnimatedRef,
  useDerivedValue,
  useSharedValue,
} from 'react-native-reanimated';
import {
  ETypeUpdate,
  ICreatePost,
  initParamCreatePost,
} from '@/store/updates/type';
import {HeaderCreatePost} from '@/screens/CreatePostScreen/HeaderCreatePost';
import {getChannels, useChannelsByQuery} from '@/store/channels';
import {ImageDTO} from '@/components/CameraRollPicker/useGallery';
import {IBase64, uploadImage} from '@/utils/helper';
import {getUpdates} from '@/store/updates';
import {Buffer} from 'buffer';
import {DiscardDialog} from '@/components/DiscardDialog';
import {requestCreatePost, requestEditPost} from '@/store/updates/function';
import {MyInputBackground} from '@/screens/CreateEventScreen/MyInputBackground';
import {BottomSheetMenu} from '@/components/BottomSheets';
import {BottomSheetModal} from '@gorhom/bottom-sheet';
import {MenuItem} from '@/components/MenuItem';
import {AvatarWithSelectChannel} from '@/components/AvatarWithSelectChannel';
import LoadingModal from '@/components/LoadingModal';
import {View} from 'react-native';

export interface CreatePostScreenParams {
  type: 'update' | 'styled' | 'welcome' | 'idea' | 'announcement' | 'external';
  channel_id?: string;
  id?: number;
}

export const CreatePostScreen = memo(function () {
  const {type, id, channel_id} = useNavigationParams<CreatePostScreenParams>();
  const ref = useRef<AnimatedKeyboardAvoidingViewRef>(null);
  const scrollRef = useAnimatedRef<any>();
  const params = useSharedValue<ICreatePost>(initParamCreatePost);
  const [visibleDialog, showDialog, hideDialog] = useBoolean();
  const bottomSheetRef = useRef<BottomSheetModal | null>(null);
  const channelsOption = useChannelsByQuery('joined').map(id => ({
    label: getChannels(id)?.name || 'Undefined channel',
    value: id,
  }));
  const [label, setLabel] = useState('');
  const imageRef = useRef<ImageDTO | null>(null);

  useEffect(() => {
    if (channelsOption && channelsOption.length > 0) {
      const selectedChannel = channelsOption.find(
        item => item.value === params.value.channel_id.toString(),
      );
      if (selectedChannel) {
        setLabel(selectedChannel.label);
      }
    }
  }, [params, setLabel, channelsOption]);

  const onSelectBackground = useCallback(
    (image: ImageDTO) => {
      imageRef.current = image;
      params.value['image-bg'] = image.url;
    },
    [imageRef, params],
  );

  const [{loading, error: createError}, onPressSubmit] =
    useAsyncFn(async () => {
      let imageUrl = params.value['image-bg'];
      if (imageRef.current) {
        imageUrl = await uploadImage(imageRef.current);
      }
      const _params = {
        ...params.value,
        metatype: type,
        'image-bg': imageUrl,
      };
      if (id) {
        await requestEditPost(_params);
      } else {
        await requestCreatePost(_params);
        ToastService.show('Create post successfully');
      }
      goBack();
    }, [id, imageRef, params]);
  useAutoToastError(createError);

  const onGoBack = useCallback(() => {
    console.log('aaa');
    goBack();
  }, []);

  useEffect(() => {
    let _params: ICreatePost = {
      ...initParamCreatePost,
    };
    if (id) {
      const update = getUpdates(id);
      const coverImage: IBase64 = JSON.parse(
        Buffer.from(
          update?.image ||
            'eyJ0eXBlIjoiIiwiY29sb3IiOiIiLCJvcGFjaXR5IjowLCJwZXJjZW50IjoxMDAsImltYWdlIjoiIiwidGV4dHMiOltdfQ',
          'base64',
        ).toString('utf-8') || '',
      );
      _params = {
        id: update?.id,
        channel_id: update?.channel_id || 0,
        metatype: type,
        content: update?.content || '',
        'file[]': [],
        // schedule?:number,
        // schedule_date?:number,
        // schedule_shift?:string,
        // usernames?:string,
        name: update?.name || '',
        'image-bg': coverImage?.image,
      };
    }
    params.value = _params;
  }, [params, type]);

  const animatedActiveBtn = useDerivedValue(() => {
    if (channel_id) {
      return !!params.value.content;
    }

    return !!params.value.content;
  }, [channel_id]);

  const renderItem = useCallback(
    (item: any, index: number) => {
      return (
        <MenuItem
          key={index}
          title={item.label}
          onPress={() => {
            onChangeValue('channel_id', item.value);
            setLabel(item.label);
            bottomSheetRef.current?.dismiss();
          }}
        />
      );
    },
    [bottomSheetRef],
  );
  const onChangeValue = useCallback(
    (key: string, value: any) => {
      runOnUI((_key: string, _value) => {
        'worklet';

        params.value = {
          ...params.value,
          [_key]: _value,
        };
      })(key, value);
    },
    [params],
  );

  return (
    <FullScreenWrapper isGrey>
      <MyKeyboardAvoidingView ref={ref} scrollRef={scrollRef}>
        <HeaderCreatePost
          title={id ? 'Edit update' : 'Create update'}
          active={animatedActiveBtn}
          onPress={onPressSubmit}
          loading={loading}
          id={id}
          onGoBack={onGoBack}
        />
        <LoadingModal isVisible={loading} />
        <SScrollView ref={scrollRef} keyboardShouldPersistTaps={'handled'}>
          <SContainer>
            <View
              style={{
                gap: 12,
              }}>
              <UIText.HeaderMedium>General information</UIText.HeaderMedium>
              <AvatarWithSelectChannel
                bottomSheetRef={bottomSheetRef}
                label={label}
                typePost={type}
              />
              <SContentContainer>
                {type === ETypeUpdate.ANNOUNCEMENT ||
                type === ETypeUpdate.WELCOME ? (
                  <MyInputBackground
                    onSelectedImage={onSelectBackground}
                    image={params.value['image-bg']}
                  />
                ) : null}
                {type == ETypeUpdate.ANNOUNCEMENT ||
                type === ETypeUpdate.IDEA ? (
                  <UIInput.Text
                    keyName={'name'}
                    params={params}
                    title={type === 'announcement' ? 'Title' : 'The idea'}
                    onChangeValue={onChangeValue}
                    placeholder={'---'}
                    required
                  />
                ) : null}
                {type == ETypeUpdate.WELCOME ? (
                  <UIInput.UserPicker
                    title={'Select people'}
                    type={'user'}
                    keyName={'usernames'}
                    params={params}
                    onChangeValue={onChangeValue}
                    multiple
                    placeholder={'---'}
                    required
                  />
                ) : null}

                {type == ETypeUpdate.UPDATE || type === ETypeUpdate.IDEA ? (
                  <SUITextInput
                    title={
                      type === 'idea'
                        ? 'Extra description'
                        : 'Share with your team'
                    }
                    keyName={'content'}
                    params={params}
                    placeholder={'---'}
                    defaultValue={''}
                    // minHeight={320}
                    required
                    multiline
                  />
                ) : (
                  <UITextInput
                    title={'Content'}
                    keyName={'content'}
                    params={params}
                    defaultValue={''}
                    required
                  />
                )}
                {/*Pinned to channel*/}
                <UIInput.Attachment
                  title={'Attach files or Images'}
                  keyName={'file[]'}
                  params={params}
                  onChangeValue={onChangeValue}
                />
              </SContentContainer>
            </View>
            <AnimatedBottomSpace />
          </SContainer>
        </SScrollView>
        <DiscardDialog
          title={'Discard change'}
          content={
            'You have made changes to this item that have not been saved. If you leave this page, your changes will be lost.'
          }
          visible={visibleDialog}
          onClose={hideDialog}
        />
        <BottomSheetMenu
          name={'channel'}
          ref={bottomSheetRef}
          items={channelsOption}
          renderMenuItem={renderItem}
          titleHeader={'Select channel'}
        />
      </MyKeyboardAvoidingView>
    </FullScreenWrapper>
  );
});

const SContainer = styled.View`
  padding: 12px 16px;
`;
const SContentContainer = styled.View`
  gap: 8px;
`;
const SScrollView = styled.ScrollView`
  flex: 1;
  background-color: white;
  margin-top: 8px;
`;
const SUITextInput = styled(UITextInput).attrs(p => ({
  textStyle: {
    minHeight: 320,
  },
}))``;
